<template>
  <div class="wizard__step wizard__step--load">
    <div
      class="row row--center"
      :style="{ 'min-height': `${minHeight}px` }"
    ></div>
  </div>
</template>

<script>
export default {
  name: "WizardLoading",
  props: {
    translations: {
      type: Object,
      default: () => {},
      required: false
    }
  },
  data() {
    return {
      minHeight: 0
    };
  },
  created() {
    let vm = this;
    window.addEventListener("resize", () => {
      vm.setHeight();
    });
  },
  mounted() {
    this.setHeight();
  },
  methods: {
    setHeight() {
      let h = window.innerHeight;
      this.minHeight = h - 340;
    }
  }
};
</script>
