<template>
  <div v-if="subscribeLists.length" class="wizard__config">
    <div class="form-group custom-select col-md-10 col-md-offset-1 mb-30">
      <div
        class="fm-custom-select"
        :class="{ 'fm-custom-select--open': select.isOpen }"
      >
        <div
          class="fm-custom-select__value"
          @click="select.isOpen = !select.isOpen"
        >
          {{ selectedListValue }}
        </div>
        <transition name="fade" mode="out-in">
          <div v-show="select.isOpen" class="fm-custom-select__dropdown">
            <ul>
              <li
                v-for="list in subscribeLists"
                :key="list.subscriberListHash"
                class="fm-custom-select__listitem"
                @click="updateCSValue(list.subscriberListHash)"
              >
                <b>{{ list.name }}</b>
                <span v-if="list.hasOwnProperty('subscribers_number')">
                  - {{ $t(translations.number_of_subscribers) }}:
                  {{ list.subscribers_number }}
                </span>
              </li>
            </ul>
          </div>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
import GET_PARENT_DATA from "$js/vue/wizard/mixins/props.js";
import WIZARD from "$js/vue/wizard/mixins/methods.js";
/*global FreshMail */

export default {
  name: "ConfigSynchronize",
  mixins: [WIZARD, GET_PARENT_DATA],
  data() {
    return {
      subscribeLists: [],
      select: {
        isOpen: false
      }
    };
  },
  computed: {
    selectedListValue() {
      let selection = this.settings.synchronize_list;
      return this.subscribeLists.find(x => x.subscriberListHash === selection)
        .name;
    }
  },
  destroyed() {
    //FreshMail.eventBus.unsubscribe("fm_setSettings");
  },
  mounted() {
    FreshMail.eventBus.subscribe("fm_subList", payload => {
      this.subscribeLists = payload.data.data;
      this.$root.$emit("input", {
        name: "synchronize_list",
        value: this.subscribeLists[0].subscriberListHash
      });
    });
    FreshMail.eventBus.emit("fm_GET", {
      url: this.links.lists,
      callback: "fm_subList"
    });
  },
  methods: {
    selectChange(event) {
      this.$root.$emit("input", {
        name: event.target.name,
        value: event.target.value
      });
    },
    updateCSValue(hash) {
      this.select.isOpen = false;
      this.$root.$emit("input", {
        name: "synchronize_list",
        value: hash
      });
    }
  }
};
</script>
