<template>
  <div class="wizard__step wizard__step--3">
    <section class="section-bg--white pt-15">
      <div class="container">
        <div class="col-md-6 col-md-offset-3 mb-50">
          <h4 class="text-center">
            {{ $t(translations.configuration_overview) }}
          </h4>
        </div>
      </div>
    </section>
    <section class="section-bg--white pt-15">
      <div class="container">
        <div class="col-md-6 col-md-offset-3">
          <ul class="configuration__summary">
            <li>
              <i :class="iconsStatus(settings.smtp)"></i>
              {{ $t(translations.freshMail_servers) }}
            </li>
            <li>
              <i :class="iconsStatus(settings.synchronize)"></i>
              {{ $t(translations.subscriber_list) }}
            </li>
            <li>
              <div
                v-if="!settings.synchronize"
                class="synchronize__notification"
              >
                <p>{{ $t(translations.full_potential) }}</p>
                <button
                  class="btn btn--yellow btn--arrow text--size-1 mt-10"
                  @click="
                    settings.logged_in ? setWizardStep(2) : setWizardStep(1)
                  "
                >
                  {{ $t(translations.change_settings) }}
                </button>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </section>
    <section class="section-bg--white pt-0 pb-0">
      <div class="footer__buttons">
        <button
          class="btn btn--white btn--arrow btn--arrow-left"
          @click="setWizardStep(2)"
        >
          {{ $t(translations.back) }}
        </button>
        <button class="btn btn--yellow btn--arrow" @click="saveSettings()">
          {{ $t(translations.finish_your_configuration) }}
        </button>
      </div>
    </section>
  </div>
</template>

<script>
import GET_PARENT_DATA from "$js/vue/wizard/mixins/props.js";
import WIZARD from "$js/vue/wizard/mixins/methods.js";
/*global FreshMail */

export default {
  name: "WizardStep3",
  mixins: [GET_PARENT_DATA, WIZARD],
  created() {
    FreshMail.eventBus.subscribe("fm_settingsSavedAfter", payload => {
      if (payload.success) {
        if (this.settings.synchronize_list !== "") {
          let synchronizeLink = `${this.links.synchronize_link}${this.settings.synchronize_list}`;
          FreshMail.eventBus.emit("fm_GET", {
            url: synchronizeLink
          });
          FreshMail.eventBus.emit("fm_GET", {
            url: this.links.activate_package
          });
        }
        FreshMail.eventBus.emit("fm_notification", {
          type: "notice",
          text: payload.message
        });
        setTimeout(() => {
          if (this.links.success_redirect && this.settings.logged_in) {
            window.location.href = this.links.success_redirect;
          } else {
            window.location.href = this.links.abandon_carts_redirect;
          }
        }, 2000);
      }
    });
  },
  methods: {
    checkboxChange(event) {
      this.$root.$emit("input", {
        name: event.target.name,
        value: event.target.checked
      });
    },
    radioChange(event, value) {
      this.$root.$emit("input", {
        name: event.target.name,
        value
      });
    },
    selectChange(event) {
      console.log(event.target);
      this.$root.$emit("input", {
        name: event.target.name,
        value: parseInt(event.target.value)
      });
    },
    saveSettings() {
      FreshMail.eventBus.emit("fm_POST", {
        url: this.links.save,
        data: this.settings,
        callback: "fm_settingsSavedAfter"
      });
    },
    iconsStatus(isActive) {
      return isActive
        ? "freshmail-ico freshmail-ico-circle freshmail-ico-check freshmail-ico-circle--green"
        : "freshmail-ico freshmail-ico-circle freshmail-ico-cross";
    }
  }
};
</script>
