<script>
import GET_PARENT_DATA from "$js/vue/wizard/mixins/props.js";
import WIZARD from "$js/vue/wizard/mixins/methods.js";
/*global FreshMail */

export default {
  name: "WizardStep1",
  mixins: [GET_PARENT_DATA, WIZARD],

  created() {
    FreshMail.eventBus.subscribe("fm_connectToFreshMailAfter", payload => {
      console.log("fm_connectToFreshMailAfter", payload);
      if (payload.success) {
        FreshMail.eventBus.emit("fm_notification", {
          type: "notice",
          text: payload.message
        });
        this.setWizardStep(2);
        FreshMail.eventBus.emit("fm_GET", {
          url: this.links.settings,
          callback: "fm_setSettings"
        });
      } else {
        this.$root.$emit("input", {
          name: "api_token",
          value: null
        });
      }
    });
  },

  methods: {
    connectToFreshMail() {
      FreshMail.eventBus.emit("fm_POST", {
        url: this.links.connect,
        data: {
          api_token: this.settings.api_token
        },
        callback: "fm_connectToFreshMailAfter"
      });
    },

    inputChange(event) {
      this.$root.$emit("input", {
        name: event.target.name,
        value: event.target.value
      });
    }
  }
};
</script>
