<template>
  <div class="wizard__step wizard__step--2">
    <template v-if="!settings.logged_in">
      <section class="section-bg--white pt-15">
        <div class="container">
          <div class="col-md-10 col-md-offset-1 mb-50">
            <h4 class="text-center">
              {{ $t(translations.discover_benefits) }}
            </h4>
            <benefitsBox />
          </div>
        </div>
      </section>
      <section class="section-bg--green pt-15">
        <div class="container">
          <h4 class="col-md-6 col-md-offset-3 text-center text--white mt-50">
            {{ $t(translations.i_wish_to_start) }}:
          </h4>
          <div
            class="col-md-4 col-md-offset-4 text-center mt-50 mb-50 d-flex d-flex justify-content-between"
          >
            <button
              class="btn btn--yellow btn--arrow btn--arrow-left mr-5 ml-5"
              @click="setWizardStep(1)"
            >
              {{ $t(translations.back_step) }} 1
            </button>
            <a class="btn btn--yellow mr-5 ml-5" href="#">{{
              $t(translations.learn_more)
            }}</a>
          </div>
        </div>
      </section>
      <section class="section-bg--grey pt-15">
        <div class="container">
          <h4 class="text-center mt-50 mb-50">
            {{ $t(translations.i_dont_want) }}:
          </h4>
          <div class="container pb-50">
            <div class="col-md-12 text-center">
              <button
                class="btn btn--yellow btn--arrow"
                @click="setWizardStep(3)"
              >
                {{ $t(translations.continue) }}
              </button>
            </div>
          </div>
        </div>
      </section>
    </template>
    <template v-else>
      <section class="section-bg--grey pt-15 mt-50">
        <div class="container">
          <div class="col-md-8 col-md-offset-2">
            <h4 class="text-center">
              {{ $t(translations.configuration_overview) }}
            </h4>
          </div>
          <div class="col-md-8 col-md-offset-2">
            <div class="row">
              <div class="col-md-6 benefits__box">
                <div class="custom-checkbox">
                  <input
                    id="fm_smtp"
                    type="checkbox"
                    name="smtp"
                    :checked="settings.smtp"
                    @change="checkboxChange($event)"
                  />
                  <label for="fm_smtp" class="text--size-15">{{
                    $t(translations.freshMail_servers)
                  }}</label>
                </div>
              </div>
              <div class="col-md-6 benefits__box">
                <div class="custom-checkbox">
                  <input
                    id="fm_synchronize"
                    type="checkbox"
                    name="synchronize"
                    :checked="settings.synchronize"
                    @change="checkboxChange($event)"
                  />
                  <label for="fm_synchronize" class="text--size-15">{{
                    $t(translations.subscriber_list)
                  }}</label>
                </div>
              </div>
            </div>
            <!-- additional wizard settings -->
            <div v-if="additionalSettingsVisibility" class="row">
              <div class="col-md-12">
                <p class="text--size-13 font-weight-300 text-center mt-30">
                  {{ $t(translations.please_select) }}
                </p>
                <keep-alive>
                  <config-synchronize v-if="settings.synchronize" />
                </keep-alive>
              </div>
            </div>
            <div class="row">
              <div class="col-md-12 text-center">
                <button
                  class="btn btn--yellow btn--arrow"
                  @click="setWizardStep(3)"
                >
                  {{ $t(translations.continue) }}
                </button>
              </div>
            </div>
            <!-- END additional wizard settings -->
          </div>
        </div>
      </section>
      <section class="section-bg--white pt-15">
        <div class="container">
          <div class="col-md-10 col-md-offset-1 mb-50">
            <h4 class="text-center">{{ $t(translations.make_full_use) }}</h4>
          </div>
          <benefitsBox />
        </div>
        <div class="footer__buttons">
          <button
            class="btn btn--white btn--arrow btn--arrow-left"
            @click="setWizardStep(1)"
          >
            {{ $t(translations.back) }}
          </button>
          <button class="btn btn--yellow btn--arrow" @click="setWizardStep(3)">
            {{ $t(translations.continue) }}
          </button>
        </div>
      </section>
    </template>
  </div>
</template>

<script>
import GET_PARENT_DATA from "$js/vue/wizard/mixins/props.js";
import WIZARD from "$js/vue/wizard/mixins/methods.js";
import configSynchronize from "./partials/configSynchronize.vue";
import benefitsBox from "./partials/benefitsBox.vue";

export default {
  name: "WizardStep2",
  components: {
    "config-synchronize": configSynchronize,
    benefitsBox
  },
  mixins: [GET_PARENT_DATA, WIZARD],
  computed: {
    additionalSettingsVisibility() {
      return this.settings.synchronize ? true : false;
    }
  },
  methods: {
    checkboxChange(event) {
      this.$root.$emit("input", {
        name: event.target.name,
        value: event.target.checked
      });
    }
  }
};
</script>
