<template>
  <div id="wizard" class="freshmail__wizard">
    <transition name="fade" mode="out-in">
      <preloader v-if="isLoading" />
    </transition>
    <section id="wizard-header" class="section-bg--white">
      <div class="wizard__header">
        <div class="wizard__progress">
          <div
            v-for="index in maxSteps"
            :key="index"
            class="wizard-step"
            :class="{
              'wizard-step--current': currentStep == index,
              'wizard-step--done': index < currentStep
            }"
          >
            {{ index }}
          </div>
        </div>
        <a
          :href="links.help"
          target="_blank"
          class="btn btn--yellow btn--arrow ml-auto"
          >{{ $t(translations.help) }}</a
        >
      </div>
    </section>
    <transition name="component-fade" mode="out-in">
      <section
        v-if="getWizardComponentName() != 'wizardLoading'"
        class="section-bg--white section-bg--nopadding"
      >
        <div class="wizard__badge">
          <img
            :src="`${module.assets_url}/img/prestashop-logo.png`"
            alt="prestashop"
          />
          {{ $t(translations.official_module) }}
        </div>
        <div class="col-md-12 header__logo">
          <img
            :src="`${module.assets_url}/img/freshmail-logo.svg`"
            alt="FreshMail"
          />
        </div>
      </section>
    </transition>

    <div class="form-wrapper" :class="direction">
      <transition name="component-fade" mode="out-in">
        <keep-alive>
          <component
            :is="getWizardComponentName()"
            :translations="translations"
          ></component>
        </keep-alive>
      </transition>
    </div>
  </div>
</template>

<script>
import preloader from "$js/vue/preloader/preloader.vue";
import WIZARD from "$js/vue/wizard/mixins/methods.js";
import wizardLoading from "./wizardLoading.vue";
import wizardStep1 from "./wizardStep1.vue";
import wizardStep2 from "./wizardStep2.vue";
import wizardStep3 from "./wizardStep3.vue";
/*global FreshMail */

export default {
  name: "WizardContainer",
  components: {
    preloader,
    wizardLoading,
    wizardStep1,
    wizardStep2,
    wizardStep3
  },
  mixins: [WIZARD],
  props: {
    translations: {
      type: Object,
      default: () => {},
      required: true
    },
    links: {
      type: Object,
      default: () => {},
      required: true
    },
    module: {
      type: Object,
      default: () => {},
      required: false
    }
  },

  data() {
    return {
      settings: {},
      maxSteps: 3,
      currentStep: 1,
      isLoading: false,
      direction: ""
    };
  },

  computed: {},

  watch: {
    currentStep: function(nval, oval) {
      let d = nval > oval ? "next" : "prev";
      this.direction = d;
    }
  },

  created() {
    let vm = this;
    (async function() {
      while (!window.FreshMail) {
        await new Promise(function(resolve) {
          return setTimeout(resolve, 100);
        });
      }
      /* Load saved settings */
      FreshMail.eventBus.subscribe("fm_loading", payload => {
        vm.isLoading = payload.status;
      });
      FreshMail.eventBus.subscribe("fm_setSettings", payload => {
        vm.settings = payload.data;
        if (payload.data.has_configured_before == true) {
          FreshMail.eventBus.emit("fm_notification", {
            type: "notice",
            text: vm.translations.has_configured_before
          });
        }
      });
      FreshMail.eventBus.subscribe("fm_getSettings", () => {
        vm.getSettings();
      });
    })();
    /* VUE */
    this.$root.$on("input", payload => {
      this.settings[payload.name] = payload.value;
    });
    this.$root.$on("setStep", payload => {
      this.currentStep = payload.value;
      window.scrollTo({ top: 0, behavior: "smooth" });
    });
  },

  mounted() {
    let vm = this;
    setTimeout(function() {
      vm.getSettings();
    }, 200);
  },

  methods: {
    prevStep() {
      if (this.currentStep == 1) return false;
      this.currentStep -= 1;
    },

    nextStep() {
      if (this.currentStep == this.maxSteps) return false;
      this.currentStep += 1;
    },

    getWizardComponentName() {
      if (
        Object.keys(this.settings).length === 0 &&
        this.settings.constructor === Object
      ) {
        return "wizardLoading";
      } else {
        return `wizardStep${this.currentStep}`;
      }
    },

    getSettings() {
      FreshMail.eventBus.emit("fm_GET", {
        url: this.links.settings,
        callback: "fm_setSettings"
      });
    }
  }
};
</script>
